.grid-container {
    display: grid;
    grid-template-columns: repeat(4, minmax(0, 1fr));
    grid-auto-columns: auto;
    grid-column-gap: 25px;
    align-content: center;

    /* Media query for screens smaller than 768px */
    @media (max-width: 768px) {
        grid-template-columns: repeat(1, minmax(0, 1fr)); /* Change to a single column layout */
        gap: 20px; /* Adjust the gap for smaller screens */
    }

    /* Media query for screens between 769px and 1024px */
    @media (min-width: 769px) and (max-width: 1024px) {
        grid-template-columns: repeat(2, minmax(0, 1fr)); /* Change to a two-column layout */
        gap: 20px; /* Adjust the gap for medium-sized screens */
    }

    /* Media query for screens larger than 1024px */
    @media (min-width: 1025px) {
        grid-template-columns: repeat(4, minmax(0, 1fr)); /* Return to the original four-column layout */
        grid-column-gap: 25px; /* Restore the gap for larger screens */
    }
}

.grid-container > div {
    background-color: #85b282;
    text-align: center;
    padding: 30px 0 0 0;
    width: auto;
}

hr {
    width: 25%;
    margin: 1rem auto;
    border: 2px solid white;
    justify-items: center;
}

.h {
    font-size: 1.5rem;
    font-weight: 600;
    font-family: 'Fraunces', serif;
    color: #85b282;
}

.grid-container img {
    width: 50%;
    height: auto;
    object-fit: cover;
}

.grid-item {
    border-top-left-radius: 1.5rem;
    border-top-right-radius: 1.5rem;
    border-bottom-left-radius: 1.5rem;
    border-bottom-right-radius: 1.5rem;
}

